.preloader__backdrop {
  width: 100%;
  height: 100%;
  z-index: 99;
  background-color: rgba(0, 0, 0, .4);
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out, transform .3s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader__backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: scale(.9);
}

.preloader {
  width: 100px;
  height: 100px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preloader :first-child {
  background: #19a68c;
  animation-delay: -1.5s;
}

.preloader :nth-child(2) {
  background: #f63d3a;
  animation-delay: -1s;
}

.preloader :nth-child(3) {
  background: #fda543;
  animation-delay: -.5s;
}

.preloader :last-child {
  background: #193b48;
}

.preloader hr {
  width: 40%;
  height: 40%;
  border: 0;
  border-radius: 50%;
  margin: 0;
  animation: spin 2s infinite;
  position: absolute;
}

@keyframes spin {
  0%, 100% {
    transform: translate(0);
  }

  25% {
    transform: translate(160%);
  }

  50% {
    transform: translate(160%, 160%);
  }

  75% {
    transform: translate(0, 160%);
  }
}

